@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

body{margin:0;padding:0}.demos{font-family:'Roboto',  'Helvetica Neue Light', 'Open Sans', Helvetica;display:flex;color:#353C44}.demos nav{width:350px;min-height:1200px;box-sizing:border-box;border-right:1px solid #CBDCE4}.demos nav .logo-container{padding:20px 40px;height:20px;border-bottom:1px solid #CBDCE4;display:flex}.demos nav .logo-container a{line-height:27px}.demos nav .logo-container b,.demos nav .logo-container a{margin-right:10px}.demos nav ul{list-style-type:none;padding:0;box-sizing:border-box}.demos nav ul a{color:#53585B;display:block;padding:10px 0 10px 40px;text-decoration:none}.demos nav ul a .menu-button{display:flex}.demos nav ul a .menu-button .menu-icon{opacity:0.6;width:40px}.demos nav ul a .menu-button .menu-button-inner{width:185px}.demos nav ul a.active{pointer-events:none;padding:10px 0 10px 36px;border-left:4px solid #353C44;color:#353C44}.demos nav ul a.active .menu-button .menu-icon{opacity:1}.demos main{width:calc(100% - 350px);box-sizing:border-box}.demos main .simulator-content{background:#ECF0F4;padding:28px 80px 60px}.demos main .simulator-content h1{margin:0 0 30px 20px;font-size:22px}.demos main .simulator-content .ka{border-radius:12px;box-shadow:5px 15px 30px rgba(0,0,0,0.1)}.demos main .simulator-content .ka-tbody{max-height:600px}.demos main .code{padding:60px 80px}

.button{cursor:pointer;opacity:0.5}.button:hover{opacity:1}

.custom-cell-image{width:40px;height:40px;object-fit:cover;border-radius:25px}

